/* eslint no-eval: 0 */

import React, { useState } from "react";
import "./App.css";

const App = () => {
  const [result, setResult] = useState("");

  const handleClick = (e) => {
    setResult(result.concat(e.target.name));
  };

  const clear = () => {
    setResult("");
  };

  const backspace = () => {
    setResult(result.slice(0, -1));
  };

  const calculate = () => {
    try {
      setResult(eval(result).toString());
    } catch (err) {
      setResult("Error");
    }
  };

  return (
    <div className="App">
      <div className="sun">
        <div className="cloud1"></div>
        <div className="cloud2"></div>
        <div className="cloud3"></div>
        <div className="cloud4"></div>
      </div>
      <div className="container">
        <form value="form">
          <input type="text" defaultValue={result} />
        </form>
        <div className="keypad">
          <button
            className="highlight"
            defaultValue="clear"
            onClick={clear}
            id="clear"
          >
            Clear
          </button>
          <button
            className="highlight"
            defaultValue="C"
            onClick={backspace}
            id="backspace"
          >
            C
          </button>
          <button
            className="highlight"
            defaultValue="divide"
            name="/"
            onClick={handleClick}
          >
            &divide;
          </button>
          <button name="7" defaultValue="7" onClick={handleClick}>
            7
          </button>
          <button name="8" defaultValue="8" onClick={handleClick}>
            8
          </button>
          <button name="9" defaultValue="9" onClick={handleClick}>
            9
          </button>
          <button
            className="highlight"
            defaultValue="times"
            name="*"
            onClick={handleClick}
          >
            &times;
          </button>
          <button name="4" defaultValue="4" onClick={handleClick}>
            4
          </button>
          <button name="5" defaultValue="5" onClick={handleClick}>
            5
          </button>
          <button name="6" defaultValue="6" onClick={handleClick}>
            6
          </button>
          <button
            className="highlight"
            defaultValue="ndash"
            name="-"
            onClick={handleClick}
          >
            &ndash;
          </button>
          <button name="1" defaultValue="1" onClick={handleClick}>
            1
          </button>
          <button name="2" defaultValue="2" onClick={handleClick}>
            2
          </button>
          <button name="3" defaultValue="3" onClick={handleClick}>
            3
          </button>
          <button
            className="highlight"
            defaultValue="highlight"
            name="+"
            onClick={handleClick}
          >
            +
          </button>
          <button name="0" defaultValue="0" onClick={handleClick}>
            0
          </button>
          <button name="." defaultValue="." onClick={handleClick}>
            .
          </button>
          <button
            className="highlight"
            defaultValue="result"
            onClick={calculate}
            id="result"
          >
            =
          </button>
        </div>
      </div>
    </div>
  );
};

export default App;
